<template>
   <v-app>
      <v-app-bar app :flat=true v-if="$route.name == 'viewer'">
         <v-row align="center">
            <v-col>
               Certify Log Viewer
            </v-col>
            <v-col align="right">
               <v-btn 
                  v-bind:color="testMode ? 'success' : undefined"
                  v-bind:outlined="testMode ? false : true"
                  @click="toggleTestMode()" 
               >
                  Test Mode
               </v-btn>
            </v-col>
         </v-row>
      </v-app-bar>
      
      <v-main>
         <router-view />     
      </v-main>

   </v-app>
   
</template>

<script>
import Viewer from './components/Viewer';
import Login from './components/Login';
import io from 'socket.io-client';

//var socket = io('http://localhost:3000');

export default {
   name: 'App',

   components: {
      Viewer,
      Login
   },

   data: function () {
      return {
         testMode: false,
         socket: io('https://kozametrics.com')
      }
   },

   methods: {
      toggleTestMode: function() {
         this.testMode = !this.testMode;
         this.socket.emit('testMode', this.testMode);
      }
   },

   provide() {
      return {
         socket: this.socket
      }
   }
};

</script>
