<template>
   <v-container fluid>
      <v-data-table
         :headers="headers"
         :items="messages"
      ></v-data-table>
   </v-container> 
</template>

<script>
//import io from 'socket.io-client';

export default {
   name: 'Viewer',
   inject: ['socket'],

   data: function () {
      return {
         headers: [
            { text: 'Type', value: 'Type'},
            { text: 'Shard', value: 'ShardID'},
            { text: 'Company', value: 'CompanyPK'},
            { text: 'Emp', value: 'EmpPK'},
            { text: 'Severity', value: 'Severity'},
            { text: 'Message', value: 'Message'}
         ],

         messages: []
      }
   },

   created() {
      // console.log('socket connected');

      this.socket.on('message', (msg) => {
         //console.log('got msg: ' + msg);
         this.messages.push(msg);
      });
   },

   methods: {

   }
   
}
</script>