import Vue from 'vue'
import Router from 'vue-router'

import Login from './components/Login.vue'
import Viewer from './components/Viewer.vue'

Vue.use(Router);

export default new Router({
   base: process.env.NODE_ENV === 'production' ? '/c-log-viewer/' : '/',
   mode: 'history',
   routes: [
     {
       path: '/',
       name: 'login',
       component: Login,
     },
 
     {
       path: '/viewer',
       name: 'viewer',
       component: Viewer,
     }
   ]
 })